
import { computed, defineComponent, ref } from "vue";
export default defineComponent({
  props: {
    colors: {
      type: Array,
      default: () => ["#e67e22", "#3498db", "#f1c40f"],
    },
    title: {
      type: String,
      default: "",
    },
  },
  setup(props) {
    const strCom = computed(() => {
      return props.title.split("");
    });

    return { strCom };
  },
});
